import React from "react";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import axios from "axios";
import {buildApiUrl} from "./App";
import {faUndo} from "@fortawesome/free-solid-svg-icons/faUndo";

export class Undo extends React.Component {

    onClickUndo = () => {
        let loadConversationLog = this.props.loadConversationLog;
        this.undo(() => {
            loadConversationLog();
        });
    };

    onKeyPress = (e) => {
        if (e.keyCode === 13) {
            this.onClickUndo();
        }
    };

    undo = async (onComplete) => {
        await axios.post(buildApiUrl('undo')).then(onComplete);
    };

    render() {
        let ret = <span/>;
        if (this.props.isUndoAvailable) {
            ret = <FontAwesomeIcon icon={faUndo} onClick={this.onClickUndo} className="undoIcon"/>
        }

        return (ret);
    }
}
