import axios from 'axios';
import {activePowerModeAnimation, buildApiUrl,} from "./App";
import TypeIt from "typeit-react";
import * as React from "react";

export class InputField extends React.Component {
    constructor(props) {
        super(props);
        this.inputRef = props.inputRef;
        this.submitRef = props.submitRef;
    }

    componentDidMount() {
        activePowerModeAnimation();
    }

    sayToBot = async () => {
        if (this.props.isConversationEnded) {
            this.props.loadConversationLog();
            this.props.setConversationEnded(false);
        } else {

            let userInput = this.props.isQuickReplyInput ? this.props.userInput : this.inputRef.current.value;

            if (userInput !== '') {
                let res = await axios.post(buildApiUrl(), {input: userInput});

                this.props.setConversationState(res.data.conversationState);

                if (res.data.conversationState === 'ENDED') {
                    this.props.setUndoAvailable(false);
                } else {
                    this.props.setUndoAvailable(res.data.conversationSteps.length > 1);
                }

                let conversationOutputs = res.data.conversationOutputs;

                let conversationOutput;
                if (conversationOutputs.length > 0) {
                    conversationOutput = conversationOutputs[conversationOutputs.length - 1];

                    if (typeof conversationOutput.output == "undefined") {
                        conversationOutput.output = [ // default fallback
                            {
                                "text": "I am so sorry ðŸ˜¥",
                                "type": "text"
                            },
                            {
                                "text": "But this is only a demo...",
                                "type": "text"
                            },
                            {
                                "text": "Use the back button â¬… and try me again ðŸ‘€âœŒ",
                                "type": "text"
                            }
                        ]
                    }

                    this.props.updateBotOutputs(conversationOutput);
                }

                if (!this.props.isQuickReplyInput && this.inputRef.current != null) {
                    this.inputRef.current.value = '';
                }
            }
        }
    };

    keyPress = (e) => {
        if (e.keyCode === 13) {
            this.sayToBot();
        }
    };

    render() {

        let inputField;
        if (this.props.isConversationEnded) {
            inputField =
                <div className="chatbotOutput">
                    Conversation has Ended
                </div>
        } else if (this.props.isQuickReplyInput) {
            inputField =
                <TypeIt
                    id="animatedUserInputField"
                    element={"input"}
                    getBeforeInit={instance => {
                        instance.type(this.props.userInput);
                        return instance;
                    }}
                    onKeyDown={this.keyPress}
                    autoComplete="off"
                    className="inputField"
                    tabIndex={this.props.tabIndexInput}
                    options={{
                        speed: 30,
                        waitUntilVisible: true,
                        lifeLike: true,
                        cursor: true,
                        afterComplete: this.props.makeVisibleReplyButtonClick
                    }}
                />
        } else {
            inputField =
                <input id="userInputField"
                       onKeyDown={this.keyPress}
                       autoComplete="off"
                       ref={this.inputRef}
                       className="inputField"
                       tabIndex={this.props.tabIndexInput}/>
        }

        return (
            <div className="inputFieldContainer">
                {inputField}
                <button type="submit"
                        ref={this.submitRef}
                        onClick={this.sayToBot}
                        tabIndex={this.props.tabIndexSubmit}
                        className={(this.props.isClickAnimation ? 'onClickAnimation' : '') +
                        (this.props.isConversationEnded ? ' btnRestartConversation' : '')}>
                    {this.props.isConversationEnded ? 'restart conversation' : 'reply'}
                </button>
            </div>
        );
    }
}
