import React from 'react';
import axios from "axios";
import {v4 as uuidv4} from 'uuid';

import botIcon from './bot_green_heart.png';
import './App.css';

import {InputField} from "./InputField";
import {BotOutputs} from "./BotOutputs";
import {QuickReplies} from "./QuickReplies";
import {Undo} from "./Undo";
import PowerModeInput from "power-mode-input";


export let userId = uuidv4();

export const eddiApiUrl = `https://app.labs.ai/managedbots/partygrouping/`;

let index = 0;

export let buildApiUrl = (path) => {
    if (!path) {
        path = '';
    }

    if (!path.startsWith('/')) {
        path = '/' + path;
    }
    return `${eddiApiUrl}${userId}${path}?returnCurrentStepOnly=false`;
}

export let activePowerModeAnimation = () => {
    PowerModeInput.make('.inputField', {
        height: 2,
        tha: [0, 360],
        g: 0.1,
        num: 1,
        radius: 2,
        circle: true,
        alpha: [0.75, 0.1],
        color: "#67AB67"
    });
}

export class App extends React.Component {

    conversationState;

    constructor(props, context) {
        super(props, context);
        this.state = {
            userInput: '',
            botOutputs: [],
            quickReplies: [],
            isConversationEnded: false,
            isBotOutputAnimationFinished: false,
            isUndoAvailable: false,
            isQuickReplyInput: false,
            isClickAnimation: false,
            tabIndex: 1
        };

        this.botOutputsRef = React.createRef();
        this.inputRef = React.createRef();
        this.submitRef = React.createRef();
    }

    componentDidMount() {
        this.loadConversationLog();
        this.botOutputsRef.current.focus();
    }

    loadConversationLog = async () => {
        this.clearBotOutputAnimationFinished();

        let res = await axios.get(buildApiUrl());

        this.setConversationState(res.data.conversationState);

        if (res.data.conversationState === 'ENDED') {
            this.setUndoAvailable(false);
        } else {
            this.setUndoAvailable(res.data.conversationSteps.length > 1);
        }

        let conversationOutputs = res.data.conversationOutputs;

        if (conversationOutputs.length > 0) {
            let conversationOutput = conversationOutputs[conversationOutputs.length - 1];

            if (conversationOutput.output) {
                this.setState({botOutputs: this.addIndex(conversationOutput.output)});
            }

            if (conversationOutput.quickReplies) {
                this.setState({quickReplies: this.addIndex(conversationOutput.quickReplies)});
            }
        }
    };

    addIndex = (elements) => {
        if (elements) {
            return elements.map(element => ({
                ...element,
                index: index++,
            }));
        } else return [];
    };

    updateBotOutputs = (conversationOutput) => {
        this.clearBotOutputAnimationFinished();
        this.setState({botOutputs: this.addIndex(conversationOutput.output)});
        this.setState({quickReplies: this.addIndex(conversationOutput.quickReplies)});
        this.setQuickReplyInput(false);
    };
    removeQuickReplies = () => {
        this.setState({quickReplies: []});
        this.setState({isBotOutputAnimationFinished: false});
    };

    setBotOutputAnimationFinished = () => {
        this.setState({isBotOutputAnimationFinished: true});

        if (this.conversationState === 'ENDED') {
            this.setConversationEnded(true);
        } else {
            this.setConversationEnded(false);
        }
    };

    clearBotOutputAnimationFinished = () => {
        this.setState({isBotOutputAnimationFinished: false});
    };

    setUndoAvailable = (isUndoAvailable) => {
        this.setState({isUndoAvailable: isUndoAvailable});
    }

    setConversationEnded = (isConversationEnded) => {
        this.setState({isConversationEnded: isConversationEnded});
    }

    setConversationState = (conversationState) => {
        this.conversationState = conversationState;
    }

    setUserInput = (userInput) => {
        this.setState({userInput: userInput});
        this.setQuickReplyInput(true);
    }

    setQuickReplyInput = (isQuickReplyInput) => {
        this.setState({isQuickReplyInput: isQuickReplyInput});
        if (!this.state.isConversationEnded) {
            activePowerModeAnimation();
        }
    }

    makeVisibleReplyButtonClick = () => {
        setTimeout(() => {
            this.animateButtonClick();
            this.submitRef.current.click();
        }, 500);
    }

    animateButtonClick = () => {
        this.setState({isClickAnimation: true});
        setTimeout(() => {
            this.setState({isClickAnimation: false});
        }, 500);
    }

    render() {
        return (
            <div className="App">
                <img src={botIcon} className="botIcon" alt="logo icon"/>

                <Undo isUndoAvailable={this.state.isUndoAvailable}
                      loadConversationLog={this.loadConversationLog}/>

                <BotOutputs outputs={this.state.botOutputs}
                            botOutputsRef={this.botOutputsRef}
                            setBotOutputAnimationFinished={this.setBotOutputAnimationFinished}/>

                <QuickReplies quickReplies={this.state.quickReplies}
                              inputRef={this.inputRef}
                              removeQuickReplies={this.removeQuickReplies}
                              isBotOutputAnimationFinished={this.state.isBotOutputAnimationFinished}
                              loadConversationLog={this.loadConversationLog}
                              setUserInput={this.setUserInput}/>

                <InputField inputRef={this.inputRef}
                            submitRef={this.submitRef}
                            tabIndexInput={index + 1}
                            tabIndexSubmit={index + 2}
                            userInput={this.state.userInput}
                            updateBotOutputs={this.updateBotOutputs}
                            setUndoAvailable={this.setUndoAvailable}
                            isQuickReplyInput={this.state.isQuickReplyInput}
                            makeVisibleReplyButtonClick={this.makeVisibleReplyButtonClick}
                            isClickAnimation={this.state.isClickAnimation}
                            setConversationState={this.setConversationState}
                            setConversationEnded={this.setConversationEnded}
                            isConversationEnded={this.state.isConversationEnded}
                            loadConversationLog={this.loadConversationLog}/>
            </div>
        );
    }
}

export default App;
