import React from "react";
import TypeIt from "typeit-react";
import Delay from "react-delay";

export class BotOutput extends React.Component {
    render() {
        return (
            <span className="chatbotOutput" tabIndex={this.props.tabIndex}>
                <Delay wait={1500}>
                    <TypeIt options={{
                        strings: [this.props.output.text],
                        speed: 30,
                        waitUntilVisible: true,
                        lifeLike: true,
                        cursor: false,
                        afterComplete: this.props.startQuickReplyAnimation
                    }}>
                    </TypeIt>
                </Delay>
            </span>
        );
    }
}
