import React from "react";
import {BotOutput} from "./BotOutput";
import Delay from "react-delay/lib/Delay";
import {AnimatePresence, motion} from "framer-motion";

export class BotOutputs extends React.Component {
    render() {
        let time = 3000;
        let times = 0;
        let outputElements =
            this.props.outputs.map((output, index, arr) =>
                <motion.div key={output.index} initial={false} exit={{y: -300, opacity: 0}}
                            transition={{duration: 0.3}}>
                    <Delay wait={(time * times++)}>
                        <motion.div initial={{y: 300}} animate={{y: 150}} transition={{duration: 2}}>
                            <BotOutput output={output}
                                       tabIndex={output.index + 1}
                                       startQuickReplyAnimation={index === arr.length - 1 ?
                                           this.props.setBotOutputAnimationFinished : () => {}}/>
                        </motion.div>
                    </Delay>
                </motion.div>
            );

        return (
            <span className="botOutputsContainer" ref={this.props.botOutputsRef}>
                <AnimatePresence>
                    {outputElements}
                </AnimatePresence>
            </span>
        );
    }
}
