import React from "react";
import {motion} from "framer-motion";

export class QuickReply extends React.Component {

    setTextToInputField = () => {
        this.props.setUserInput(this.props.quickReply.value);
    };

    onClickQuickReply = () => {
        this.setTextToInputField();
        this.props.removeQuickReplies();
    };

    onKeyPress = (e) => {
        if (e.keyCode === 13) {
            this.onClickQuickReply();
        }
    };

    render() {
        return (
            <motion.div initial={{x: 200, opacity: 0}}
                        animate={{x: 0, opacity: 1}}
                        transition={{duration: 0.5}}
                        className="containerEunit"
                        tabIndex={this.props.tabIndex}
                        onKeyDown={this.onKeyPress}>
                    <span className="quickReply" onClick={this.onClickQuickReply}>
                        {this.props.quickReply.value}
                    </span>
            </motion.div>
        );
    }
}
